@import "../../theme.scss";

.rahmen-content {
  height: 100%;
  display: flex;
  flex-direction: column;

  .rahmen-main {
    flex: 1;
    overflow-y: auto;
  }

  .cover-Mask {
    position: fixed;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.2);
    z-index: 199;
    text-align: center;

    &::before {
      content: "";
      height: 100%;
      display: inline-block;
      vertical-align: middle;
    }

    .cover-inner {
      width: 600px;
      background-color: #fff;
      display: inline-block;
      vertical-align: middle;
      text-align: left;
      padding: 20px;

      .preview_picture {
        overflow: hidden;

        .preview_left {
          float: left;

          .show-img {
            height: 300px;
            width: 400px;
          }

          .preview_btn {
            display: flex;
            justify-content: space-around;
            margin-top: 15px;

            span {
              padding: 5px 15px;
              color: #fff;
              cursor: pointer;
              background-color: $bg-color;
              border-radius: 3px;
            }
          }
        }

        .preview_right {
          float: right;
          width: 145px;
          height: 300px;
          padding-left: 20px;
          border-left: 1px solid #333;

          .before {
            border: 5px solid #fff;
            box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.15);
            width: 126px;
            height: 174px;
            overflow: hidden;
          }
        }
      }
    }
  }

  .icon-xuanzhong1 {
    color: $bg-color;
  }
}